.availableFoods {
    max-width: 60rem;
    width: 90%;
    margin: 2rem auto;
    animation: meals-appear 1s ease-out forwards;
    overflow-y: auto;
    height: 48vh;
}

.availableFoods::-webkit-scrollbar {
    width: 0.5em;
}

.availableFoods::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(241, 82, 82, 0.3);
}

.availableFoods::-webkit-scrollbar-thumb {
    background-color: #c14a4a;
    border-radius: 2rem;
    outline: 1px solid #8dbdee;
}

.availableFoods ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.foodsLoading {
    text-align: center;
}

.error {
    text-align: center;
    color: red;
}

@keyframes meals-appear {
    from {
        opacity: 0;
        transform: translateY(3rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}